import React from "react";
import {connect} from "react-redux";
import {getFilterConfiguration, getSelectedProducer, getSelectedFilter, getSelectedStickerFilter, getActualStickerFilters} from "../../selectors";
import {changeStickerFilterAttribute, changeStickerFilter, fetchFilterConfiguration, setSelectedProducer,getFilteredProductList, getFilteredProductTree, getFilteredProductTreeWithoutReloadStickers, clearAttrsFilters} from "eshop/modules/simfree/actions/filter";
import StickerFilterView from "eshop/modules/simfree/components/filter/StickerFilterView";

const mapStateToProps = state => ({
    filterConfiguration: getFilterConfiguration(state),
    filters: getActualStickerFilters(state),
    selectedFilters: getSelectedFilter(state),
    selectedStickerFilters: getSelectedStickerFilter(state),
    selectedProducer: getSelectedProducer(state),
    isModal: false
});

const mapDispatchToProps = (dispatch) => ({
    reloadTree:(page) => dispatch(getFilteredProductTreeWithoutReloadStickers(page)),
    setSelectedProducer: (selectedProducer) => dispatch(setSelectedProducer(selectedProducer)),
    getFilteredList:(page) => dispatch(getFilteredProductList(page)),
    changeStickerFilterAttribute: (element, parent, multiValue, operation) => dispatch(changeStickerFilterAttribute(element, parent, multiValue, operation)),
    changeStickerFilter: (value) => dispatch(changeStickerFilter(value)),
    clearAttrFilters:() => dispatch(clearAttrsFilters())
});

const StickerFilterComponent = connect(mapStateToProps, mapDispatchToProps)(StickerFilterView);
export default StickerFilterComponent;